.cfooter
  margin-top: 80px
  background-color: $dark-color
  padding: 45px 0
  @media (max-width: 420px)
    margin-top: 0
    padding: 43px 0
  .ccontainer
    display: flex
    align-items: flex-start
    justify-content: flex-start
    flex-wrap: wrap
  &__top
    display: flex
    align-items: flex-start
    justify-content: flex-start
    flex-wrap: wrap
    &__column
      flex: 0 0 calc(33.3% - 20px)
      max-width: calc(33.3% - 20px)
      margin: 0 30px 30px 0
      @media (max-width: 768px)
        flex: 0 0 calc(50% - 20px)
        max-width: calc(50% - 20px)
        margin: 0 20px 30px 0
      @media (max-width: 420px)
        flex: 0 0 100%
        max-width: 100%
        margin: 0 0 25px 0 !important
      &:nth-of-type(3n)
        margin-right: 0
        @media (max-width: 768px)
          margin-right: 20px
      &:nth-of-type(2n)
        @media (max-width: 768px)
          margin-right: 0
      &:nth-of-type(3)
        @media (max-width: 768px)
          flex: 0 0 100%
          max-width: 100%
          display: flex
          flex-direction: row
          align-items: flex-start
          justify-content: flex-start
          flex-wrap: wrap
        .menu-item
          @media (max-width: 768px)
            flex: 0 0 calc(50% - 20px)
            max-width: calc(50% - 20px)
            margin: 0 20px 20px 0
          @media (max-width: 420px)
            flex: 0 0 100%
            max-width: 100%
            margin: 0 0 25px 0 !important
          &:nth-of-type(2n)
            margin-right: 0
          &:nth-of-type(2)
            order: 3
            @media (max-width: 420px)
              order: 0
      .menu-item
        margin-top: 30px
        @media (max-width: 420px)
          margin-top: 25px
        &:nth-of-type(1)
          margin-top: 0
        .menu-title
          font-family: $default-font
          font-weight: 700
          font-size: 14px
          line-height: 20px
          color: $pink-color
          letter-spacing: .03em
          text-transform: uppercase
          @media (max-width: 420px)
            display: block
            width: 100%
            position: relative
            &:after
              content: '\e80b'
              position: absolute
              top: 0
              right: 3px
              font-family: Fontello
              font-style: normal
              font-weight: normal
              speak: never
              display: block
              margin: 0
              text-decoration: inherit
              text-align: center
              font-variant: normal
              text-transform: none
              font-size: 8px
              line-height: 20px
            & + ul
              display: none
              margin: 12px 0 0
            &.show
              &:after
                transform: scale(1,-1)
        ul
          margin: 8px 0 0
          padding: 0
          list-style-type: none
          @media (max-width: 420px)
            margin: 0
          li
            display: block
            margin-top: 11px
            @media (max-width: 768px)
              margin-top: 8px
            @media (max-width: 420px)
              margin-top: 23px
            &:nth-of-type(1)
              margin-top: 0
            a
              color: #fff
              text-decoration: none
              font-family: $default-font
              font-size: 14px
              line-height: 20px
              letter-spacing: .03em
              @media (max-width: 768px)
                line-height: 18px
              &:hover
                text-decoration: underline
        &.main-item
          ul
            li
              margin-top: 8px
              @media (max-width: 420px)
                margin-top: 23px
              &:nth-of-type(1)
                margin-top: 0
              a
                font-family: $default-font
                font-weight: 700
                font-size: 14px
                line-height: 20px
                color: $pink-color
                letter-spacing: .03em
                text-transform: uppercase
                position: relative
                transition: color .3s
                &:before
                  content: ''
                  display: block
                  width: 0
                  height: 1px
                  background-color: $pink-color
                  position: absolute
                  bottom: 0
                  left: 0
                  transition: width .3s, background-color .3s
                &:hover
                  color: #fff
                  text-decoration: none
                  &:before
                    width: 100%
                    background-color: #fff
  &__bottom
    margin-top: 8px
    padding: 38px 0
    border-top: 1px solid rgba(255, 255, 255, 0.2)
    @media (max-width: 1024px)
      padding: 38px 0 0
    @media (max-width: 768px)
      padding: 15px 0 0
    &__column
      flex: 0 0 calc(33.3% - 20px)
      max-width: calc(33.3% - 20px)
      margin: 0 30px 60px 0
      @media (max-width: 1024px)
        margin: 0 30px 35px 0
      @media (max-width: 768px)
        flex: 0 0 50%
        max-width: 50%
        margin: 0 0 35px 0
      @media (max-width: 420px)
        flex: 0 0 100%
        max-width: 100%
      &:nth-of-type(3n)
        margin-right: 0
      &:nth-of-type(1)
        @media (max-width: 768px)
          flex: 0 0 100%
          max-width: 100%
          margin: 0 auto 20px
      .social-links
        display: flex
        align-items: center
        justify-content: flex-start
        margin: 0
        padding: 15px 0
        list-style-type: none
        @media (max-width: 768px)
          justify-content: center
        &__item
          flex: 0 0 auto
          width: auto
          margin: 0 57px 0 0
          &:last-child
            margin-right: 0
        &__link
          color: #fff
          font-size: 21px
          line-height: 25px
          text-decoration: none
          transition: color .3s
          .icon.icon-youtube-play
            font-size: 20px
          &:hover
            color: $pink-color
      .backcall-btn
        border-color: $pink-color
        display: block
        width: 100%
        max-width: 255px
        margin: 0
        color: #fff
        font-weight: 700
        font-size: 16px
        line-height: 16px
        padding: 15px 0
        @media (max-width: 768px)
          margin: 0 auto
      .apps-links
        display: flex
        align-items: center
        justify-content: flex-start
        flex-wrap: wrap
        padding: 10px 0
        @media (max-width: 768px)
          justify-content: center
        &__item
          flex: 0 0 auto
          width: auto
          &:nth-of-type(1)
            margin-right: 20px
        &__link
          display: block
          transition: transform .3s
          &:hover
            transform: translateY(-5px)
          .app-image
            display: block
            width: auto
            max-width: 100%
    .copyright
      display: inline-block
      flex: 0 0 auto
      width: auto
      font-family: $default-font
      font-size: 12px
      line-height: 12px
      letter-spacing: .01em
      color: #fff
      @media (max-width: 1024px)
        margin-top: 25px
        order: 3
      @media (max-width: 768px)
        flex: 0 0 100%
        text-align: center
        margin-top: 30px
    .payment-list
      flex: 0 0 auto
      width: auto
      display: flex
      align-items: center
      justify-content: center
      margin: 0 auto
      @media (max-width: 1024px)
        margin: 0 auto 0 0
      @media (max-width: 420px)
        width: 100%
      &__item
        flex: 0 0 auto
        width: auto
        margin: 0 7px
        .payment-image
          display: block
          width: auto
          max-width: 100%
    .info-links
      flex: 0 0 auto
      width: auto
      display: flex
      align-items: center
      justify-content: flex-end
      @media (max-width: 420px)
        flex-direction: column
        align-items: flex-start
        justify-items: flex-start
        margin-top: 20px
      &__item
        display: inline-block
        flex: 0 0 auto
        width: auto
        margin-left: 15px
        @media (max-width: 420px)
          margin-left: 0
          margin-top: 15px
        &:nth-of-type(1)
          margin-left: 0
      &__link
        font-family: $default-font
        font-size: 12px
        line-height: 14px
        color: #fff
        letter-spacing: .03em
        &:hover
          text-decoration: none
