@import "settings"
@import "_mixins/_mixins"
@import "_components/ui"

/* main page */
.cstart-form
  margin-top: 105px
  @media (max-width: 768px)
    margin-top: 165px
  @media (max-width: 640px)
    max-width: 500px
    margin: 200px auto 0
  @media (max-width: 576px)
    max-width: 100%
  @media (max-width: 420px)
    margin: 160px auto 0
  .ccontainer
    max-width: 1300px
    background-color: $pink-color
    border-radius: 30px
    padding: 0
    position: relative
    @media (max-width: 1200px)
      max-width: calc(100% - 30px)
    @media (max-width: 420px)
      max-width: 100%
      border-radius: 0
    &:before
      content: url("../images/common/main/girl1.png")
      position: absolute
      bottom: calc(100% - 59px)
      left: calc(50% - 192px)
      @media (max-width: 1200px)
        transform: scale(0.75)
        bottom: calc(100% - 35px)
        left: calc(50% - 165px)
      @media (max-width: 960px)
        left: calc(50% - 65px)
      @media (max-width: 640px)
        left: 0
      @media (max-width: 420px)
        left: 13px
        transform: scale(.67)
        bottom: calc(100% - 74px)
    &:after
      content: url("../images/common/svg/green_arrow.svg")
      position: absolute
      bottom: calc(100% - 30px)
      left: calc(55% - 12px)
      @media (max-width: 960px)
        left: calc(65% + 25px)
      @media (max-width: 640px)
        left: 220px
      @media (max-width: 420px)
        left: 208px
        transform: scale(0.75)
        bottom: calc(100% - 48px)
  &__content
    display: flex
    align-items: stretch
    justify-content: flex-start
    margin: 0 auto
    overflow: hidden
    @media (max-width: 960px)
      flex-wrap: wrap
    &__column
      flex: 0 0 auto
      max-width: 30%
      padding: 62px 0 57px
      @media (max-width: 1200px)
        padding: 30px 0 30px
      &:nth-of-type(1)
        flex: 0 0 40%
        max-width: 40%
        padding-left: 97px
        @media (max-width: 1200px)
          padding-left: 40px
        @media (max-width: 1024px)
          padding-left: 30px
        @media (max-width: 960px)
          flex: 0 0 50%
          max-width: 50%
        @media (max-width: 640px)
          flex: 0 0 100%
          max-width: 100%
          padding: 55px 55px 15px
        @media (max-width: 420px)
          padding: 55px 15px 15px
      &:nth-of-type(2)
        margin-left: 3.75%
        @media (max-width: 1200px)
          margin-left: 1.25%
        @media (max-width: 1024px)
          margin-left: 0
        @media (max-width: 960px)
          flex: 0 0 50%
          max-width: 50%
          padding: 30px
        @media (max-width: 640px)
          flex: 0 0 100%
          max-width: 100%
          padding: 0 55px 35px
          margin-top: 23px
        @media (max-width: 420px)
          padding: 0 15px
      &:nth-of-type(3)
        flex: 0 0 auto
        width: auto
        display: flex
        flex-direction: column
        align-items: center
        justify-content: center
        padding: 0 97px 0 30px
        position: relative
        @media (max-width: 1200px)
          padding: 0 30px
        @media (max-width: 960px)
          display: none
        @media (max-width: 640px)
          display: block
          flex: 0 0 100%
          max-width: 100%
          padding: 100px 55px
        @media (max-width: 420px)
          padding: 100px 55px 60px
        &:after
          content: url("../images/common/svg/radial.svg")
          position: absolute
          width: auto
          height: auto
          top: 52%
          left: 25%
          margin-right: -50%
          transform: translate(-50%, -50%)
          @media (max-width: 640px)
            left: -15%
            margin: 0 50%
  &__title
    font-family: $default-font
    font-weight: 700
    font-size: 31px
    line-height: 36px
    letter-spacing: .02em
    color: #fff
    @media (max-width: 1024px)
      font-size: 25px
      line-height: 32px
    @media (max-width: 420px)
      font-size: 22px
      line-height: 25px
      text-align: center
  &__subtitle
    display: inline-block
    color: #fff
    font-family: $default-font
    font-weight: 700
    font-size: 20px
    line-height: 29px
    letter-spacing: .03em
    text-align: center
    margin-bottom: 40px
    position: relative
    z-index: 1
    &:before
      content: url("../images/common/svg/star1.svg")
      display: block
      position: absolute
      bottom: calc(100% + 10px)
      left: 27px
    &:after
      content: url("../images/common/svg/star2.svg")
      display: block
      position: absolute
      top: calc(100% + 26px)
      right: 8px
    span
      text-transform: uppercase
  &__list
    margin-top: 44px
    @media (max-width: 420px)
      margin-top: 20px
    ul
      margin: 0
      padding: 0
      list-style-type: none
      color: #fff
      font-family: $default-font
      font-size: 16px
      line-height: 21px
      letter-spacing: .02em
      @media (max-width: 1024px)
        font-size: 15px
      @media (max-width: 420px)
        font-size: 16px
      li
        display: block
        position: relative
        padding-left: 32px
        margin-top: 21px
        @media (max-width: 1024px)
          margin-top: 15px
        @media (max-width: 420px)
          padding-left: 34px
          margin-top: 22px
        &:nth-of-type(1)
          margin-top: 0
        &:before
          content: '\e801'
          font-family: Fontello
          font-style: normal
          font-weight: normal
          speak: never
          display: block
          margin: 0
          text-decoration: inherit
          text-align: center
          font-variant: normal
          text-transform: none
          color: #fff
          position: absolute
          top: 0
          left: -2px
          font-size: 18px
          line-height: 18px
          @media (max-width: 420px)
            top: 2px
            left: 1px
  &__form
    max-width: 350px
    position: relative
    z-index: 1
    @media (max-width: 640px)
      max-width: 100%
    .input-group
      display: block
      width: 100%
      margin-top: 26px
      position: relative
      @media (max-width: 420px)
        margin-top: 31px
      &:nth-of-type(1)
        margin-top: 0
      &.check-group
        margin-top: 14px
      input
        width: 100%
      .input-message
        display: block
        position: absolute
        top: calc(100% + 2px)
        left: 0
        color: #000
        font-family: $default-font
        font-size: 12px
        line-height: 12px
        letter-spacing: .025em
        margin: 0
        .icon
          display: inline-block
          font-size: 16px
          line-height: 16px
          margin-right: 13px
          position: relative
          &:after
            content: ''
            display: block
            width: 90%
            height: 90%
            background-color: #fff
            position: absolute
            top: 5%
            left: 5%
            border-radius: 50%
            z-index: -15
.cplatform-block
  margin-top: 83px
  @media (max-width: 420px)
    margin-top: 55px
  &__title
    font-size: 36px
    line-height: 41px
    @media (max-width: 420px)
      font-size: 22px
      line-height: 25px
      text-align: center
  &__content
    display: flex
    align-items: stretch
    justify-content: flex-start
    flex-wrap: wrap
    background: $gradient
    margin-top: 40px
    border-radius: 30px
    padding: 40px 90px
    @media (max-width: 1024px)
      padding: 40px 80px
    @media (max-width: 768px)
      padding: 30px
    @media (max-width: 420px)
      margin-top: 30px
      padding: 37px 30px 30px
      border-radius: 25px
    &__left-side
      flex: 0 0 50%
      max-width: 50%
      @media (max-width: 640px)
        flex: 0 0 100%
        max-width: 100%
      h3
        font-family: $default-font
        font-size: 24px
        line-height: 30px
        text-transform: uppercase
        font-weight: 400
        letter-spacing: .04em
        @media (max-width: 420px)
          text-align: center
          font-size: 20px
          line-height: 25px
      ul
        margin: 41px 0 0
        padding: 0
        list-style-type: none
        font-family: $default-font
        font-size: 16px
        line-height: 24px
        letter-spacing: .03em
        max-width: 355px
        @media (max-width: 768px)
          font-size: 14px
          line-height: 20px
        @media (max-width: 640px)
          max-width: 100%
        @media (max-width: 420px)
          margin: 18px 0 0
          font-size: 16px
          line-height: 24px
        li
          display: block
          position: relative
          margin-top: 24px
          @media (max-width: 1024px)
            margin-top: 15px
          @media (max-width: 768px)
            padding-left: 35px
          @media (max-width: 420px)
            padding-left: 33px
            margin-top: 24px
          &:nth-of-type(1)
            margin-top: 0
          &:before
            content: '\e801'
            font-family: Fontello
            font-style: normal
            font-weight: normal
            speak: never
            display: block
            margin: 0
            text-decoration: inherit
            text-align: center
            font-variant: normal
            text-transform: none
            color: #fff
            position: absolute
            top: 2px
            left: -47px
            font-size: 18px
            line-height: 18px
            @media (max-width: 768px)
              left: 0
      .buy-btn
        margin: 20px 0 0 5px
        @media (max-width: 1024px)
          margin: 30px 0 0 0
        @media (max-width: 420px)
          margin: 35px 0 0 0
          display: block
          width: 100%
    &__right-side
      flex: 0 0 50%
      max-width: 50%
      display: flex
      flex-direction: column
      align-items: center
      justify-content: center
      padding-top: 60px
      @media (max-width: 1024px)
        padding-top: 25px
      @media (max-width: 640px)
        flex: 0 0 100%
        max-width: 100%
        margin-top: 15px
      h3
        padding-left: 15px
        font-family: $default-font
        font-weight: 700
        font-size: 20px
        line-height: 23px
        color: #000
        letter-spacing: .02em
        @media (max-width: 1024px)
          padding-left: 0
          text-align: center
        @media (max-width: 420px)
          font-size: 16px
          line-height: 18px
      ul
        display: flex
        align-items: center
        justify-content: center
        flex-wrap: wrap
        margin: 35px auto 0
        padding: 0
        list-style-type: none
        @media (max-width: 420px)
          margin: 20px -10px 0
          width: calc(100% + 20px)
        li
          display: inline-block
          border: 2px solid #fff
          text-align: center
          padding: 19px 13px
          text-transform: uppercase
          font-family: $default-font
          font-size: 16px
          line-height: 20px
          text-transform: uppercase
          border-radius: 20px
          letter-spacing: .03em
          color: #fff
          margin: 0 9px 18px 9px
          @media (max-width: 1024px)
            margin: 0 5px 10px
            padding: 12px 10px
          @media (max-width: 768px)
            font-size: 14px
            padding: 10px
          @media (max-width: 420px)
            font-size: 10px
            line-height: 12px
            border-radius: 12px
            padding: 12px 12px
            margin: 0 2px 10px
          &:nth-of-type(1)
            margin: 0 18px 18px
            @media (max-width: 1024px)
              margin: 0 10px 10px

.ctraining-types
  margin-top: 80px
  @media (max-width: 420px)
    margin-top: 43px
  &__title
    font-family: $default-font
    font-weight: 700
    font-size: 20px
    line-height: 23px
    letter-spacing: .03em
    color: #000
    @media (max-width: 420px)
      text-align: center
      font-size: 18px
      line-height: 21px
      max-width: 280px
      margin: 0 auto
  &__content
    display: flex
    align-items: stretch
    justify-content: flex-start
    flex-wrap: wrap
    margin-top: 40px
    @media (max-width: 420px)
      margin-top: 20px
    &__item
      flex: 0 0 calc(33.3% - 20px)
      max-width: calc(33.3% - 20px)
      margin: 0 30px 0 0
      border-radius: 30px
      padding: 40px 33px
      display: flex
      flex-direction: column
      align-items: flex-start
      justify-content: flex-start
      min-height: 313px
      @media (max-width: 1024px)
        padding: 30px
      @media (max-width: 768px)
        padding: 20px
        flex: 0 0 calc(33.3% - 10px)
        max-width: calc(33.3% - 10px)
        margin: 0 15px 0 0
        min-height: 300px
      @media (max-width: 640px)
        flex: 0 0 100%
        max-width: 100%
        margin: 0 0 20px
        min-height: auto
        padding: 30px 32px
        border-radius: 25px
      &:nth-of-type(3n)
        margin-right: 0
      &:nth-of-type(1n)
        background-color: rgba($green-color, .5)
      &:nth-of-type(2n)
        background-color: rgba($blue-color, .5)
      &:nth-of-type(3n)
        background-color: rgba($pink-color, .5)
      .item-title
        font-family: $default-font
        font-size: 24px
        line-height: 30px
        font-weight: 400
        text-transform: uppercase
        letter-spacing: .035em
        @media (max-width: 768px)
          font-size: 20px
          line-height: 28px
        @media (max-width: 420px)
          display: block
          width: 100%
          line-height: 25px
          text-align: center
      .item-descr
        font-family: $default-font
        font-size: 16px
        line-height: 25px
        margin-top: 10px
        letter-spacing: .03em
        margin-bottom: 20px
        @media (max-width: 768px)
          font-size: 15px
          line-height: 22px
        @media (max-width: 420px)
          margin-top: 15px
          font-size: 16px
          line-height: 25px
          margin-bottom: 0
      .item-btn
        margin-top: auto
        padding: 12px 30px
        min-width: 187px
        @media (max-width: 420px)
          display: block
          width: 100%
          max-width: 100%
          margin-top: 18px
.ccurses-block
  margin-top: 80px
  position: relative
  @media (max-width: 420px)
    max-width: calc(100% - 30px)
    margin: 40px auto 0
  &:before
    content: ''
    display: block
    width: 100%
    height: calc(100% - 42% + 4px)
    position: absolute
    top: calc(42% - 4px)
    left: 0
    background-color: $gray-darkness-color
    @media (max-width: 420px)
      width: calc(100% + 30px)
      height: calc(100% - 240px)
      left: -15px
      top: 240px
  .ccontainer
    background-color: $gray-color
    border-radius: 40px
    max-width: 1110px
    padding: 32px 32px 80px
    position: relative
    z-index: 1
    @media (max-width: 1024px)
      padding: 32px 32px 50px
    @media (max-width: 420px)
      border-radius: 20px
      padding: 40px 30px
  &__title
    margin-top: 48px
    line-height: 38px
    letter-spacing: .025em
    max-width: 730px
    @media (max-width: 1024px)
      margin-top: 0
      max-width: 100%
    @media (max-width: 960px)
      max-width: 500px
    @media (max-width: 420px)
      font-size: 20px
      line-height: 23px
      text-align: center
  &__choose
    margin-top: 40px
    @media (max-width: 420px)
      margin-top: 20px
    h3
      font-family: $default-font
      font-weight: 700
      font-size: 16px
      line-height: 20px
      color: #000000
      letter-spacing: .02em
      @media (max-width: 420px)
        font-weight: 400
        font-size: 16px
        line-height: 20px
    .choose-select
      font-family: $default-font
      font-weight: 700
      font-size: 12px
      line-height: 12px
      margin-top: 15px
      display: block
      width: 100%
      max-width: 256px
      cursor: pointer
      @media (max-width: 420px)
        max-width: 100%
        margin-top: 17px
        .jq-selectbox__select
          background-color: transparent
          border: 1px solid #DBDEE0
          border-radius: 4px
          .jq-selectbox__select-text
            padding: 12px 35px 10px 10px
          .jq-selectbox__trigger
            width: 45px

  &__tabs
    margin-top: 40px
    &__item
      display: none
      align-items: stretch
      justify-content: flex-start
      flex-wrap: wrap
      &.active
        display: flex
      .tab-title
        font-family: $default-font
        font-weight: 700
        font-size: 20px
        line-height: 23px
        color: #000
        letter-spacing: .025em
        @media (max-width: 420px)
          font-size: 16px
          line-height: 18px
          text-align: center
      .tab-descr
        margin-top: 35px
        padding-left: 15px
        @media (max-width: 1024px)
          padding-left: 0
        @media (max-width: 420px)
          margin-top: 22px
        ul
          margin: 0
          padding: 0
          list-style-type: none
          font-family: $default-font
          font-size: 16px
          line-height: 24px
          letter-spacing: .02em
          @media (max-width: 768px)
            font-size: 14px
            line-height: 22px
          @media (max-width: 420px)
            font-size: 16px
            line-height: 24px
          li
            display: block
            position: relative
            padding-left: 48px
            margin-top: 24px
            @media (max-width: 1024px)
              padding-left: 35px
            @media (max-width: 960px)
              margin-top: 15px
            @media (max-width: 768px)
              margin-top: 10px
            @media (max-width: 420px)
              margin-top: 24px
            &:nth-of-type(1)
              margin-top: 0
            &:before
              content: '\e801'
              font-family: Fontello
              font-style: normal
              font-weight: normal
              speak: never
              display: block
              margin: 0
              text-decoration: inherit
              text-align: center
              font-variant: normal
              text-transform: none
              font-size: 18px
              line-height: 18px
              position: absolute
              top: 1px
              left: 0
              color: $pink-color
    .tab
      &__left-side
        flex: 0 0 50%
        max-width: 50%
        padding-right: 45px
        @media (max-width: 1200px)
          padding-right: 15px
        @media (max-width: 640px)
          flex: 0 0 100%
          max-width: 100%
          padding: 0
          margin-top: 30px
      &__right-side
        flex: 0 0 50%
        max-width: 50%
        padding-left: 15px
        @media (max-width: 1024px)
          padding-left: 0
        @media (max-width: 768px)
          display: flex
          align-items: center
          justify-content: center
          margin-top: 50px
        @media (max-width: 640px)
          flex: 0 0 100%
          max-width: 100%
          order: -1
          margin-top: 0
        .tab-image
          display: block
          width: auto
          max-width: 100%
          margin: 0
          @media (max-width: 1024px)
            margin-left: auto
          @media (max-width: 640px)
            margin: 0 auto 0 0
            border-radius: 25px

  &__btn-row
    width: 100%
    text-align: center
    margin-top: 55px
    @media (max-width: 420px)
      margin-top: 40px
    .all-curses
      display: inline-block
      font-family: $default-font
      font-weight: 700
      font-size: 14px
      line-height: 18px
      letter-spacing: .03em
      color: $txt-color
      transition: color .3s
      &:hover
        color: $pink-color
        text-decoration: none
.ccards-block
  background-color: $gray-darkness-color
  padding: 80px 0
  @media (max-width: 420px)
    padding: 60px 0
  .title-row
    display: flex
    align-items: flex-end
    justify-content: flex-end
    flex-wrap: wrap
    @media (max-width: 640px)
      flex-direction: column
      align-items: center
      justify-content: center
      text-align: center
  &__title
    display: inline-block
    margin-right: auto
    line-height: 37px
    @media (max-width: 1024px)
      flex: 0 0 50%
      max-width: 50%
    @media (max-width: 768px)
      font-size: 28px
      line-height: 35px
      flex: 0 0 auto
    @media (max-width: 640px)
      max-width: 100%
      br
