  &__subtitle
    font-family: Veles, sans-serif
    font-weight: 700
    font-size: 30px
    line-height: 30px
    color: $pink-color
    @media (max-width: 1024px)
      flex: 0 0 370px
      max-width: 370px
      line-height: 40px
    @media (max-width: 768px)
      font-size: 25px
      line-height: 32px
      margin-left: auto
      flex: 0 0 330px
      max-width: 330px
    @media (max-width: 640px)
      max-width: 100%
      margin: 24px auto 0 0
      flex: 0 0 auto
    @media (max-width: 420px)
      font-size: 20px
      line-height: 27px
      flex: 0 0 auto
      max-width: 300px
      margin: 24px auto 0
  &__content
    @media (max-width: 768px)
      padding-left: 15px
    @media (max-width: 420px)
      padding: 0 15px
    .ccards-slider
      margin-top: 40px
      @media (max-width: 420px)
        margin-top: 22px
      .swiper-wrapper
        margin-left: -80px
        @media (max-width: 1024px)
          margin-left: -20px
        @media (max-width: 960px)
          margin-left: 10px
        @media (max-width: 768px)
          margin-left: -15px
        @media (max-width: 420px)
          margin-left: 0
    .slide-item
        max-width: 920px
        width: 100%
        margin-right: 30px
        @media (max-width: 768px)
          max-width: calc(100% - 60px)
        @media (max-width: 420px)
          max-width: 100%
          margin-right: 0
        &__content
          display: flex
          align-items: stretch
          justify-content: flex-start
          border-radius: 30px
          background-color: #fff
          overflow: hidden
          @media (max-width: 420px)
            flex-direction: column
            align-items: flex-start
            justify-items: flex-start
          &__left-side
            flex: 0 0 62%
            max-width: 62%
            padding: 42px 65px 35px
            @media (max-width: 1024px)
              padding: 35px 40px
            @media (max-width: 768px)
              padding: 30px
            @media (max-width: 420px)
              flex: 0 0 auto
              width: 100%
              max-width: 100%
              padding: 20px 33px
            .cards-title
              font-family: $default-font
              color: #000
              font-weight: 400
              font-size: 24px
              line-height: 30px
              text-transform: uppercase
              letter-spacing: .03em
              @media (max-width: 420px)
                font-size: 20px
