        font-size: 16px
        line-height: 21px
        color: $txt-color
        letter-spacing: .025em
        li
          display: block
          margin-top: 20px
          position: relative
          padding-left: 35px
          @media (max-width: 640px)
            margin-top: 15px
          @media (max-width: 420px)
            margin-top: 21px
          &:before
            content: '\e801'
            font-family: Fontello
            font-style: normal
            font-weight: normal
            speak: never
            display: block
            margin: 0
            text-decoration: inherit
            text-align: center
            font-variant: normal
            text-transform: none
            font-size: 18px
            line-height: 18px
            position: absolute
            top: 0
            left: 0
            color: $txt-color
          &:nth-of-type(1)
            margin-top: 0
