        @media (max-width: 768px)
          padding: 30px 20px
          border-radius: 30px
        @media (max-width: 640px)
          max-width: 460px
          margin: 0
          padding: 40px
        @media (max-width: 420px)
          border-radius: 40px
          padding: 20px 25px 35px
        .form-title
          font-family: $default-font
          font-weight: 700
          font-size: 20px
          line-height: 23px
          color: $txt-color
          letter-spacing: .02em
          @media (max-width: 420px)
            font-size: 18px
            line-height: 21px
            margin-top: 15px
