.clesson-modal
  background-color: #fff
  border-radius: 40px
  max-width: 917px
  margin: 0 auto
  padding: 55px 62px
  min-height: 575px
  @media (max-width: 768px)
    padding: 35px
    border-radius: 20px
    min-height: 530px
  @media (max-width: 640px)
    max-width: 450px
    min-height: auto
    padding: 0
  @media (max-width: 420px)
    max-width: calc(100% - 30px)
    border-radius: 40px
  .modal__close
    position: absolute
    top: 16px
    right: 16px
    padding: 0
    z-index: 10
    @media (max-width: 420px)
      right: 20px
    .icon
      color: #1C1B1F
      font-size: 24px
      line-height: 24px
      opacity: .5
      transition: opacity .3s
      &:hover
        opacity: 1
  .modal__wrapper
    padding: 32px 60% 32px 0
    @media (max-width: 640px)
      padding: 32px
    @media (max-width: 420px)
      padding: 25px
    .modal__title
      font-family: $default-font
      font-weight: 700
      font-size: 26px
      line-height: 30px
      color: $pink-color
      letter-spacing: .03em
      @media (max-width: 420px)
        text-align: center
        font-size: 20px
        line-height: 23px
        margin-top: 25px
    .modal__descr
      margin-top: 29px
      ul
        margin: 0
        padding: 0
        list-style-type: none
        font-family: $default-font
        font-size: 16px
        line-height: 21px
        color: $txt-color
        letter-spacing: .025em
        li
          display: block
          margin-top: 21px
          position: relative
          padding-left: 35px
          @media (max-width: 640px)
            margin-top: 15px
          @media (max-width: 420px)
            margin-top: 21px
          &:before
            content: '\e801'
            font-family: Fontello
            font-style: normal
            font-weight: normal
            speak: never
            display: block
            margin: 0
            text-decoration: inherit
            text-align: center
            font-variant: normal
            text-transform: none
            font-size: 18px
            line-height: 18px
            position: absolute
            top: 0
            left: 0
            color: $txt-color
          &:nth-of-type(1)
            margin-top: 0
      .gift
        display: flex
        align-items: center
        justify-content: flex-start
        flex-wrap: wrap
        margin-top: 25px
        @media (max-width: 420px)
          margin-top: 15px
          margin-bottom: 15px
        .gift-icon
          display: block
          flex: 0 0 65px
          max-width: 65px
          margin: 0
        p
          display: inline-block
          flex: 0 0 calc(100% - 65px)
          max-width: calc(100% - 65px)
          width: auto
          padding-left: 12px
          font-family: $default-font
          font-size: 16px
          line-height: 22px
          letter-spacing: .025em
    .modal__form
      position: absolute
      top: 0
      right: 0
      max-width: calc(55% - 5px)
      background-color: #F0F0F0
      border-radius: 40px
      padding: 38px 43px
      @media (max-width: 768px)
        padding: 30px 35px
      @media (max-width: 640px)
        position: relative
        max-width: 100%
        margin-top: 30px
        padding: 20px 20px
        border-radius: 20px
      @media (max-width: 420px)
        max-width: calc(100% + 50px)
        width: calc(100% + 50px)
        margin: 30px -25px 0
        padding: 15px 25px
        border-radius: 40px
      .form-title
        text-align: center
        font-family: $default-font
        font-weight: 700
        font-size: 20px
        line-height: 23px
        color: $txt-color
        @media (max-width: 420px)
          font-size: 18px
          line-height: 21px
          margin-top: 15px
        & + .input-group
          margin-top: 20px
      .input-group
        width: 100%
        margin-top: 25px
        input
          display: block
          width: 100%
          padding: 14px 25px
        &.btn-group
          button
            padding: 18px
        &.check-group
          margin-top: 18px
          label
            color: #000
            a
              color: #000
            span
              &:before
                border: 2px solid #000
            input:checked+span:before
              color: #000

.cperson-modal
  background-color: #fff
  border-radius: 40px
  max-width: 792px
  margin: 0 auto
  padding: 60px
  @media (max-width: 640px)
    padding: 30px
    max-width: 450px
  @media (max-width: 420px)
    max-width: calc(100% - 30px)
    border-radius: 40px
    padding: 45px 25px 25px
  .modal__close
    position: absolute
    top: 16px
    right: 24px
    padding: 0
    z-index: 10
    @media (max-width: 420px)
      top: 20px
      right: 22px
    .icon
      color: #1C1B1F
      font-size: 24px
      line-height: 24px
      opacity: .5
      transition: opacity .3s
      &:hover
        opacity: 1
  .mperson-info
    display: flex
    align-items: flex-start
    justify-items: flex-start
    flex-wrap: wrap
    &__image
      flex: 0 0 180px
      max-width: 180px
      @media (max-width: 640px)
        flex: 0 0 120px
        max-width: 120px
      @media (max-width: 420px)
        flex: 0 0 100px
        max-width: 100px
      .person-image
        display: block
        width: auto
        margin: 0
        max-width: 100%
        border-radius: 50%
    &__name
      flex: 0 0 calc(100% - 180px)
      max-width: calc(100% - 180px)
      padding-left: 42px
      @media (max-width: 640px)
        flex: 0 0 100%
        max-width: 100%
        padding-left: 0
      .person-name
        display: block
        margin-top: 10px
        font-family: $default-font
        font-weight: 700
        font-size: 18px
        line-height: 27px
        letter-spacing: .025em
        @media (max-width: 640px)
          position: absolute
          top: 30px
          left: 30px
          width: calc(100% - 60px)
          height: 120px
          padding-left: 150px
          display: flex
          align-items: center
          justify-content: flex-start
          margin-top: 0
        @media (max-width: 420px)
          padding-left: 120px
          height: 100px
          top: 45px
          left: 25px
      .audio-block
          width: 380px
          box-shadow: none
          background-color: transparent
          height: auto
          padding: 0
          margin-top: 16px
          border-radius: 0
          @media (max-width: 640px)
            margin-right: 0
            width: 100%
          @media (max-width: 420px)
            margin-top: 10px
          .holder
            .play-pause-btn
              display: flex
              align-items: center
              justify-items: center
              width: 42px
              height: 42px
              border-radius: 50%
              background-color: $pink-color
              transition: background-color .3s
              position: relative
              &:after
                content: '\e805'
                font-family: Fontello
                font-style: normal
                font-weight: normal
                speak: never
                display: block
                margin: 50%
                transform: translate(-50%, -50%)
                text-decoration: inherit
                text-align: center
                font-variant: normal
                text-transform: none
                color: #fff
                position: absolute
                top: 0
                left: 1px
                font-size: 18px
                line-height: 20px
              svg
                display: none
                max-width: 12px
              &[aria-label="Pause"]
                &:after
                  display: none
                svg
                  display: block
                  margin: 0 auto
                  path
                    fill: #fff
              &:hover
                background-color: $pink-darkness-color
          .controls
            position: relative
            @media (max-width: 640px)
              margin-right: 0
            &__current-time
              position: absolute
              right: 12px
              font-family: Roboto,sans-serif
              font-size: 10px
              line-height: 12px
              bottom: 4px
              @media (max-width: 640px)
                right: 0
            &__total-time
              display: none
            &__slider
              margin-left: 5px
              height: 2px
              background-color: #747474
              @media (max-width: 640px)
                margin-right: 0
              .gap-progress
                background-color: $pink-color
              .controls__progress .progress__pin
                background-color: $pink-color
          .volume
            display: none
      .try-block
        margin-top: 16px
        padding-top: 16px
        display: flex
        align-items: center
        justify-items: flex-start
        border-top: 2px solid #E3E7EB
        @media (max-width: 420px)
          flex-wrap: wrap
          margin-top: 20px
        span
          display: inline-block
          font-family: $default-font
          font-size: 14px
          line-height: 21px
          letter-spacing: .025em
          color: #000
          flex: 1 1 auto
          width: auto
          @media (max-width: 420px)
            flex: 0 0 100%
            width: 100%
        .try-btn
          display: block
          width: 222px
          white-space: nowrap
          @media (max-width: 420px)
            width: 100%
            margin-top: 10px
    &__descr
      margin-top: 78px
      display: flex
      align-items: stretch
      justify-items: flex-start
      flex-wrap: wrap
      @media (max-width: 640px)
        margin-top: 40px
      @media (max-width: 420px)
        margin-top: 30px
      &__left-side
        flex: 0 0 60%
        max-width: 60%
        padding-right: 15px
        @media (max-width: 640px)
          flex: 0 0 100%
          max-width: 100%
          padding-right: 0
      &__right-side
        flex: 0 0 calc(35% - 10px)
        max-width: calc(35% - 10px)
        margin-left: auto
        @media (max-width: 640px)
          flex: 0 0 100%
          max-width: 100%
          margin: 20px 0
      .info-item
        font-family: $default-font
        font-size: 14px
        line-height: 21px
        color: #747F87
        letter-spacing: .025em
        p
          margin-top: 10px
          span
            font-weight: 700
            color: #000
          &:nth-of-type(1)
            margin-top: 0
        ul
          margin: 18px 0 0
          padding: 0
          list-style-type: none
          font-weight: 700
          color: #000
          li
            display: block
            margin-top: 18px
            &:nth-of-type(1)
              margin-top: 0
        .message-block
          margin-top: 30px
          color: $txt-color
          max-height: 180px
          overflow: hidden
          text-overflow: ellipsis
          transition: height .5s
          &.show
            max-height: 800px
        .message-show
          display: flex
          align-items: center
          justify-content: center
          width: 100%
          .show-btn
            display: flex
            align-items: center
            justify-content: center
            text-align: center
            color: #fff
            font-size: 8px
            line-height: 24px
            width: 24px
            height: 24px
            background-color: $pink-color
            border-radius: 50%
            text-decoration: none
            transition: background-color .3s
            .icon
              display: block
              margin: 0 auto
              width: 12px
              height: 24px
              position: relative
              text-align: center
            &.open
              .icon
                transform: scale(1,-1)
            &:hover
              background-color: $pink-darkness-color